button{
  padding: 5px;
  margin: 10px;
  background-color: rgb(228, 123, 123);
  color: black;
  border-radius: 5px;
}

table {
  width: 100%;
  border-collapse: collapse;
}
th, td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}
th {
  background-color: #f2f2f2;
}

.highlight{
  background-color: aqua;
}